import React from 'react';
import './App.css';
import {
    Panel,
    View,
    Root, Header, PanelHeader,ModalRoot, ModalPage, ModalPageHeader, PanelHeaderButton, PanelHeaderBack, Div, SimpleCell, Avatar, Alert, Snackbar
} from "@vkontakte/vkui";
import '@vkontakte/vkui/dist/vkui.css';
import fire from './functions/firebase';
import checkUserExists from './functions/checkUserExist';
import bridge from '@vkontakte/vk-bridge';
import $ from 'jquery';
import getParams from './functions/getParams';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
import pivo from './img/pv.png';
import warehouse from './img/wh.png';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon24Error from '@vkontakte/icons/dist/24/error';
import Layout from "./components/Layout";
import MapMain from "./components/MapMain";
import pevkoCalc from "./functions/pevkoCalc";
import getUnix from "./functions/getUnix";


class App extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            activeView: 'main',
            activePanel: 'main',
            VK_UID: 0,
            userData: {
                id: 0,
                warPoints: 0
            },
            popout: '',
            modal: '',
            snackbar: '',
            activeTab: 0,
            activeTabOwner: {
                first_name: 'Мужик',
                last_name: 'Таджик',
                photo_200: 'https://sun9-60.userapi.com/c849532/v849532674/13d845/_Rsu5onfz-c.jpg'
            },
            points: [
                {
                    name: 'Пятачок',
                    value: 5,
                    lat: 54.791769,
                    lon: 56.086817,
                    owner: 0,
                    background: 'https://firebasestorage.googleapis.com/v0/b/game1-cae49.appspot.com/o/inorsBG%2F%D0%BF%D1%8F%D1%82%D0%B0%D0%BA.png?alt=media&token=e27c9707-bdb0-4dcf-96ec-586d00fea6eb',
                    description: 'ауе',
                    taken: 0
                }
            ]
        };
        this.openPanel = this.openPanel.bind(this);
        this.markerClick = this.markerClick.bind(this);
        this.openCaptureModal = this.openCaptureModal.bind(this);
        this.kupitRabotyag = this.kupitRabotyag.bind(this);
        this.collectPevko = this.collectPevko.bind(this);
    }
    componentDidMount() {
        this.setState({VK_UID: getParams('vk_user_id')});
        bridge.send("VKWebAppInit", {}).then(() => console.log('ВК ПОДКЛЮЧЕН'));
        checkUserExists(getParams('vk_user_id')).then(
            () => {
                fire.firestore().collection('users').doc(getParams('vk_user_id').toString()).onSnapshot(listener => {
                    this.setState({
                        userData: listener.data()
                    });
                });
                fire.firestore().collection('data').doc('points').onSnapshot(listener => {
                    if(listener.data()){
                        this.setState({
                            points: listener.data().main
                        });
                        this.pointOwnerUpdate();
                    }
                });
            }
        );
    }
    componentWillUnmount() {

    }
    kupitRabotyag(){
        const snackConfirmed = <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={{backgroundColor: 'var(--accent)'}}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
        >
            Работяги теперь доставляют певко вам
        </Snackbar>;
        const snackDeclined = <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={{backgroundColor: 'var(--field_error_border)'}}><Icon24Error fill="#fff" width={14} height={14} /></Avatar>}
        >
            Недостаточно певка для переманивания работяг
        </Snackbar>;
        this.setState({popout:
                <Alert
                    actions={[{
                        title: 'Отмена',
                        autoclose: true,
                        mode: 'cancel'
                    }, {
                        title: 'Перекупить',
                        autoclose: true,
                        action: () => {
                            let q = this.state.userData.warPoints - pevkoCalc(this.state.points[this.state.activeTab].value, this.state.points[this.state.activeTab].taken);
                            let usersCollection = fire.firestore().collection('users');
                            let owner = usersCollection.doc(this.state.points[this.state.activeTab].owner.toString());
                            if(q >= 0){
                                this.setState({snackbar: snackConfirmed});
                                let t = this.state.userData.points;
                                let s;
                                owner.get().then(
                                    o => {
                                        s = o.data().points;
                                        t.push(this.state.activeTab);
                                        s.splice(s.indexOf(this.state.activeTab), 1);

                                        owner.update(
                                            {points: s}
                                        ).then(() =>
                                            usersCollection.doc(getParams('vk_user_id').toString()).update(
                                                {points: t, warPoints: q}
                                            ).then(() => {
                                                let w = this.state.points;
                                                w[this.state.activeTab].owner = parseInt(getParams('vk_user_id'));
                                                w[this.state.activeTab].taken = getUnix();
                                                fire.firestore().collection('data').doc('points').update(
                                                    {main: w}
                                                ).then(
                                                    () => {this.pointOwnerUpdate()}
                                                );
                                            })
                                        );
                                    }
                                );
                            }
                            else{
                                this.setState({snackbar: snackDeclined})
                            }
                        }
                    }]}
                    onClose={() => {this.setState({popout: null})}}
                >
                    <h2>Подтвердите действие</h2>
                    <p>Перекупить работяг за {pevkoCalc(this.state.points[this.state.activeTab].value, this.state.points[this.state.activeTab].taken)} пива.</p>
                </Alert>
        })
    }
    openPanel(panel){
        this.setState({
            activePanel: panel
        })
    }
    openView(view){
        this.setState({
            activeView: view
        })
    }
    pointOwnerUpdate(){
        bridge.send("VKWebAppGetAuthToken", {"app_id": 7446907, "scope": ""}).then(
            v => bridge.send("VKWebAppCallAPIMethod", {"method": "users.get", "request_id": "32test", "params": {"fields":"photo_200", "user_ids": this.state.points[this.state.activeTab].owner.toString(), "v":"5.110", "access_token":v.access_token}}).then(
                v => this.setState({
                    activeTabOwner: v.response[0]
                })
            )
        );
    }
    markerClick(props, marker, e){
        if(props.name === "Склад певка"){
            alert('Собрано певка');
            return 0;
        }
        $('#layout').addClass('active');
        this.setState({
            activeTab: props.num,
        });
        this.pointOwnerUpdate();
    }
    removeIcons(mapProps, map){
        const style =
            [
                {
                    "elementType": "labels",
                    "stylers": [
                        {
                            "visibility": "off"
                        }
                    ]
                },
                {
                    "featureType": "administrative.land_parcel",
                    "stylers": [
                        {
                            "visibility": "off"
                        }
                    ]
                },
                {
                    "featureType": "administrative.neighborhood",
                    "stylers": [
                        {
                            "visibility": "off"
                        }
                    ]
                }
            ];
        map.setOptions({
            styles: style
        });
    }
    collectPevko(){
        let earned = pevkoCalc(this.state.points[this.state.activeTab].value, this.state.points[this.state.activeTab].taken);
        const snackConfirmed = <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={{backgroundColor: 'var(--accent)'}}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
        >
            Певко ({earned}) доставлено на склад
        </Snackbar>;
        const snackDeclined = <Snackbar
            layout="vertical"
            onClose={() => this.setState({ snackbar: null })}
            before={<Avatar size={24} style={{backgroundColor: 'var(--field_error_border)'}}><Icon24Error fill="#fff" width={14} height={14} /></Avatar>}
        >
            Работяги не будут напрягаться за столько певка
        </Snackbar>;
        if(earned >= 15){
            fire.firestore().collection('users').doc(getParams('vk_user_id').toString()).update({
                warPoints: this.state.userData.warPoints + earned
            }).then(
                () => {
                    let w = this.state.points;
                    w[this.state.activeTab].taken = getUnix();
                    fire.firestore().collection('data').doc('points').update(
                        {main: w}
                    ).then(
                        () => this.setState({snackbar: snackConfirmed})
                    );
                }
            );
        }
        else{
            this.setState({snackbar: snackDeclined});
        }
    }
    openCaptureModal(data){

    }

    render() {
        return (
            <Root activeView={this.state.activeView}>
                <View popout={this.state.popout} id="main" activePanel={this.state.activePanel}>
                    <Panel id="main">
                        <PanelHeader>
                            БИТВА ЗА ИНОРС
                        </PanelHeader>
                        <MapMain
                            removeIcons={this.removeIcons}
                            google={this.props.google}
                            points={this.state.points}
                            markerClick={this.markerClick}
                        />
                        <Layout
                            owner={this.state.activeTabOwner}
                            buy={this.kupitRabotyag}
                            collect={this.collectPevko}
                            data={this.state.points[this.state.activeTab]}
                        />
                        {this.state.snackbar}
                    </Panel>
                </View>
            </Root>
        );
    }
}
export default GoogleApiWrapper({
    apiKey: ("AIzaSyC2L8vPYtDn5Csq-7Gj9sZqSzvb3Bx1QfI")
})(App);
