import {Map, Marker} from "google-maps-react";
import warehouse from "../img/wh.png";
import pivo from "../img/pv.png";
import React from "react";

const MapMain = (p) => {
    return (
        <Map
            onReady={(mapProps, map) => p.removeIcons(mapProps, map)}
            style={{width: '100%', height: 'calc(100vh - 57px)'}}
            google={p.google}
            zoom={14}
            streetViewControl={false}
            mapTypeControl={false}
            scaleControl={false}
            fullscreenControl={false}
            zoomControl={false}
            initialCenter={{
                lat: 54.779270,
                lng: 56.123964
            }}
        >
            <Marker
                num={1488}
                title="Склад певка"
                name="Склад певка"
                background=""
                description=""
                taken="0"
                onClick={p.markerClick}
                position={
                    {lat: 54.777251,
                        lng: 56.127483}
                }
                icon={{
                    url: warehouse,
                    // eslint-disable-next-line no-undef
                    anchor: new google.maps.Point(32,32),
                    // eslint-disable-next-line no-undef
                    scaledSize: new google.maps.Size(32,32)
                }}
            />
            {p.points.map(value =>
                <Marker
                    key={value.name+value.lat}
                    num={value.id}
                    title={value.name}
                    name={value.name}
                    background={value.background}
                    description={value.description}
                    taken={value.taken}
                    onClick={p.markerClick}
                    position={
                        {lat: value.lat,
                            lng: value.lon}
                    }
                    icon={{
                        url: pivo
                    }}
                />
            )}
        </Map>
    )
}

export default MapMain;