import bridge from "@vkontakte/vk-bridge";

const getUnix = () => {
    bridge.send("VKWebAppGetAuthToken", {"app_id": 7446907, "scope": ""}).then(
        v => bridge.send("VKWebAppCallAPIMethod", {"method": "utils.getServerTime", "request_id": "32test", "params": {"v":"5.110", "access_token":v.access_token}}).then(
            v => {return v.response;}
        )
    );
}
export default getUnix;