import React from "react";
import {Avatar, Header, SimpleCell} from "@vkontakte/vkui";
import pivo from "../img/pv.png";
import $ from "jquery";
import Icon24Info from '@vkontakte/icons/dist/24/info';
import getParams from "../functions/getParams";
import pevkoCalc from "../functions/pevkoCalc";

const Layout = (p) => {
    return (
        <div id="layout">
            <div id="layoutBG" style={{backgroundImage: 'url('+p.data.background+')', width: '100%', height: '30vh', backgroundAttachment: 'scroll', backgroundClip: 'border-box', backgroundPosition: 'center', backgroundRepeat: 'no-repeat', backgroundSize: 'cover'}}/>
            <Header className="layoutName">{p.data.name}</Header>
            <SimpleCell before={<Icon24Info />}>{p.data.description}</SimpleCell>
            {/*<Div className="layoutDescription"></Div>*/}
            <SimpleCell before={<Avatar size={48} src={p.owner.photo_200} />} description="Владелец">{p.owner.first_name+' '+p.owner.last_name}</SimpleCell>
            <SimpleCell style={p.owner.id === parseInt(getParams('vk_user_id')) ? {display: 'none'}: {}} onClick={p.buy} multiline={true} before={<img src={pivo} alt="певко" width={24} height={24}/>} expandable={true}>Переманить работяг за {pevkoCalc(p.data.value, p.data.taken)} певка</SimpleCell>
            <SimpleCell style={p.owner.id !== parseInt(getParams('vk_user_id')) ? {display: 'none'}: {}} onClick={p.collect} multiline={true} before={<img src={pivo} alt="певко" width={24} height={24}/>} expandable={true}>Собрать {pevkoCalc(p.data.value, p.data.taken)} певка</SimpleCell>
            <div onClick={() => {$('#layout').removeClass('active')}} style={{position: 'absolute', bottom: 15, left: 'calc(50% - 36px)', boxShadow: '2px 2px 10px -1px rgba(0,0,0,0.55)', padding: 10, borderRadius: 25}}>Закрыть</div>
        </div>
    )
}

export default Layout;